export const drawScore = (ctx, x, y) => {
    ctx.font = "70px Arial bold";
    ctx.fillStyle = "white";
    ctx.fillText(x, 20, 75);
    ctx.fillText(y, 150, 75);
};

export const gameOver = (goalsPlayerOne, goalsPlayerTwo) => {
  if (goalsPlayerOne === 5 || goalsPlayerTwo === 5) {
    const score = {
      x: goalsPlayerOne,
      y: goalsPlayerTwo
    };
    Object.freeze(score);
    $("#final-score").html(`${score.x} : ${score.y}`);
    $("#congrats").removeClass("hide");
  }
};

export const playAgain = () => {
  document.location.reload();
};

export const getDistance = (x1, y1, radius1 ,x2, y2, radius2) => {
  let xDistance = x2 - x1;
  let yDistance = y2 - y1;

  let dist = Math.pow(xDistance, 2) + Math.pow(yDistance, 2);
  let rad = Math.pow(radius1 + radius2, 2);
  return (dist <= rad) ? true : false;
};



export const rotate = (velocity, angle) => {
    const rotatedVelocities = {
        x: velocity.x * Math.cos(angle) - velocity.y * Math.sin(angle),
        y: velocity.x * Math.sin(angle) + velocity.y * Math.cos(angle)
    };

    return rotatedVelocities;
};

export const resolveCollision = (player, ball) => {
    const xVelocityDiff = player.velocity.x - ball.velocity.x;
    const yVelocityDiff = player.velocity.y - ball.velocity.y;

    const xDist = ball.x - player.x;
    const yDist = ball.y - player.y;

    // Prevent accidental overlap of players
    if (xVelocityDiff * xDist + yVelocityDiff * yDist >= 0) {

        // Grab angle between the two colliding players
        const angle = -Math.atan2(ball.y - player.y, ball.x - player.x);

        // Store mass in var for better readability in collision equation
        const m1 = player.mass;
        const m2 = ball.mass;

        // Velocity before equation
        const u1 = rotate(player.velocity, angle);
        const u2 = rotate(ball.velocity, angle);

        // Velocity after 1d collision equation
        const v1 = { x: u1.x * (m1 - m2) / (m1 + m2) + u2.x * 2 * m2 / (m1 + m2), y: u1.y };
        const v2 = { x: u2.x * (m1 - m2) / (m1 + m2) + u1.x * 2 * m2 / (m1 + m2), y: u2.y };

        // Final velocity after rotating axis back to original location
        const vFinal1 = rotate(v1, -angle);
        const vFinal2 = rotate(v2, -angle);

        // Swap player velocities for realistic bounce effect
        // player.velocity.x = vFinal1.x;
        // player.velocity.y = vFinal1.y;

        ball.velocity.x = u1.x;
        ball.velocity.y = u1.y;
    }
};
